.MarketbasedTradCSS {
  * {

  }
  @rem: 144rem;
  background-color: #fff;
  .selectMounths {
    height:100/@rem;
    border-bottom: 1px solid #f4f4f4;
    background: #f4f4f4;
    line-height:100/@rem;
    text-align: center;
    display: block;
    text-align: left;
    color: #1296db;
    button{
      border:none;
      background: transparent;
      outline: none;
    }
    .selectDateBtn {
      border: none;
      text-align: left;
      width: 100px;
      display: block;
      background: transparent;
      color: #1296db;
      background: url(../images/PM/selectDowm_blueicon.png)no-repeat right;
    }
    .leftSelect{
      background: url("../images/attence/leftIcon.png");
      width: 25px;
      height: 25px;
      display: inline-block;
      margin-right: 10px;
      vertical-align: middle;
    }
    .markbasedSelect{
      background: url("../images/attence/rightIcon.png");
      width: 25px;
      height: 25px;
      display: inline-block;
      margin-left: 10px;
      vertical-align: middle;
    }
  }
  /*.commitBtn{
    border: none;
    width:30px;
    height:30px;
    color: #1296db;
    background: url(../images/goEveryIcon.png) no-repeat;
    font-size: 16px;
    display: inline-block;
  }*/
  .selectGlassMounth{
    display: inline-block;
    float: right;
    width:100/@rem;
    height:100/@rem;
    background: url(../images/goEveryIcon.png) no-repeat center;
    background-size: 100%;
    //transform: translateY(-32/@rem);

  }
  .selectGlass {
    display: inline-block;
    float: right;
    transform: translateY(-80/@rem);
    input {
      border: none;
      width: 130px;
      border: none;
      background: url(../images/PM/selectDowm_blackicon.png) no-repeat right;
      display: inline-block;
    }
  }

  .marketbasedbg{
    line-height: 50px;
    height: 50px;
    background: #ffffff;
    .lft {
      text-align: center;
      font-size: 0.28rem;
      color: #454545;
    }

  }
  .marketrbased{
    line-height: 38px;
    width: 110px;
    height: 38px;
    background: #F2F2F2;
    float: right;
    .rft{
      text-align: right;
      font-size: 0.28rem;
      color: #454545;
    }
  }
  .marketbased {
    padding: 0.2rem;
    background: #F2F2F2;
    .lft {
      text-align: center;
      font-size: 0.28rem;
      color: #454545;
    }
  }
  .marketright{
    text-align: right;
  }
  .marketList{
    background: #fff;
    margin: 20/@rem;
    border-radius: 40/@rem;
    table {
      width: 100%;
    }
    table th {
      padding: 10px 5px;
      background: #e1effc;
      vertical-align: middle;
      color: #3492e9;
      span {
        display: block;
      }
    }
    table th:first-child {
      //border-top-left-radius: 15px;
      border-right: 1px solid #d7e2ec;
      width: 70px;
    }
    table th:last-child {
      //border-top-right-radius: 15px;
    }
    table td {
      border: 1px solid #e1effc;
      border-collapse: collapse;
      padding: 10px 5px;
      text-align: center;
      vertical-align: middle;
      border-bottom: 1px solid #e1effc;
      span {
        display: block;
      }
    }
    table tr td:first-child {
      border-left: 1px solid #e1effc;
      border-right: 1px solid #e1effc;
      color: #3492e9;
    }
    table tr td:last-child {
      border-right: 1px solid #e1effc;
    }
  }

}