.KPICSS {
  @rem: 144rem;
  background-color: #f4f4f4;

  .kpiList {
    background: #fff;
    margin: 20/@rem 0/@rem;
    /*border-radius: 40/@rem;*/
  }
  .graphImg{
    height: 300px;
    text-align: center;
  }
  .kpiList ul {
    font-size: 0;
  }
  .kpiList ul li {
    display: inline-block;
    font-size: 40/@rem;
    text-align: center;
    line-height: 100/@rem;
    border-bottom: 1px solid rgba(208, 234, 234, 0.3);
  }
  .kpiList ul li span {
    display: block;
    font-size: 40/@rem;
    text-align: center;
    color: #3492e9;
  }
  .kpiList ul li:nth-child(2) {
    border-right: 1px solid rgba(208, 234, 234, 0.3);
    border-left: 1px solid rgba(208, 234, 234, 0.3);
  }
  .kpiList ul li:nth-child(5) {
    border-right: 1px solid rgba(208, 234, 234, 0.3);
    border-left: 1px solid rgba(208, 234, 234, 0.3);
  }
  .kpiList ul li:nth-child(8) {
    border-right: 1px solid rgba(208, 234, 234, 0.3);
    border-left: 1px solid rgba(208, 234, 234, 0.3);
  }
  .timeDes {
    line-height: 0.83333333rem;
    margin-right: 0.13888889rem;
    text-align: right;
    color: #3492e9;
  }
  .timeDes em {
    float: right;
    color: #3492e9;
  }
  .utis {
    line-height: 0.83333333rem;
    display: inline-block;
  }
  .graph {
    background: #fff;
    margin: 0 20/@rem 20/@rem 20/@rem;
    border-radius: 40/@rem;
    padding: 20/@rem;
  }
  .graph ul {
    display: inline-block;
    text-align: right;
    float: right;
  }
  .graph ul li {
    display: inline-block;
    line-height: 120/@rem;
    margin-right: 20/@rem;;
  }
  .graph ul li i {
    width: 40/@rem;
    height: 40/@rem;
    display: inline-block;
    vertical-align: middle;
    margin-right: 20/@rem;
    transform: translateY(-5/@rem);
  }
  .graph .sjIcon {
    background: #3492e9;
  }
  .graph .jhIcon {
    background: #3492e9;
  }
  .infoList {
    /*margin-bottom: 110px;*/
    .dayList {
      background: #fff;
      border-radius: 40/@rem;
      margin: 0 20/@rem 20/@rem 20/@rem;
      /*padding: 20/@rem;*/
      .iconbtn {
        .yearIcon {
          background: url("../images/power/day_power_icon.png") no-repeat;
          width: 45px;
          height: 41px;
          display: block;
          float: left;
        }
        .dltbBtn {
          float: right;
          background: #3492e9;
          color: #fff;
          border-top-left-radius: 0/@rem;
          border-top-right-radius: 40/@rem;
          border-bottom-right-radius: 0/@rem;
          border-bottom-left-radius: 60/@rem;
          border: none;
          width: 120/@rem;
          height: 120/@rem;
        }
        .dltbBtn .dltbicon {
          background: url("../images/power/fillForm_icon.png") no-repeat;
          width: 27px;
          height: 27px;
          display: block;
          margin: 0 auto;
        }
      }
      .litop {
        padding: 20/@rem;
        .relativeRatio {
          text-align: right;
          margin-right: 120/@rem;
          line-height: 80/@rem;
        }
        .relativeRatio em {
          color: #3492e9;
        }
      }
      .litop ul {
        font-size: 0;
        border-bottom: 2px solid #3492e9;
      }
      .litop ul li {
        font-size: 40/@rem;
        display: inline-block;
        text-align: center;
      }

      .allshow {
        padding: 0 30/@rem;
        border-bottom: 2px solid #3492e9;
        .allshowul {
          font-size: 0;
        }
        .allshowul li {
          padding: 10px 0px;
          font-size: 40/@rem;
          display: inline-block;
          text-align: center;
          vertical-align: middle;
          color: #3492e9;
        }
        .allshowul .num {
          color: #3492e9;
        }
        .allshowul .unit {
          color: #000;
        }
      }
      .lifoot {
        padding: 20/@rem;
        .dayequally {
          font-size: 0;
          border-bottom: 1px dotted #eee;
        }
        .dayequally li {
          padding: 10px 0px;
          font-size: 40/@rem;
          display: inline-block;
          text-align: center;
          color: #000;
          line-height: 80/@rem;
          vertical-align: middle;
        }
        .dayequally .name {
          color: #3492e9;
        }
      }
    }
