.OMACSS {
  * {

  }
  @rem: 144rem;
  background-color: #fff;

  .kpiheader .headerFoot {
    background: rgba(0, 0, 0, 0.5);
  }
  .omaheader{
    background: url(../images/homePage/banner3.png) no-repeat center;
    background-size: 100%;
    height: 3.5rem;
    position: relative;
  }
  .kpiheader .headerFoot ul {
    font-size: 0;
  }
  .kpiheader .headerFoot ul li {
    display: inline-block;
    font-size: 40/@rem;
    height: 140/@rem;
    line-height: 70/@rem;
    color: #fff;
    text-align: center;
    margin: 5px 0;
  }
  .kpiheader .headerFoot ul li p {
    margin: 0;
    padding: 0;
  }
  .kpiheader .headerFoot .rightBorder {
    border-right: 1px solid #eeeeee26;
  }

  //
  .navlist {
    /*margin-top: 10px;
    background: #fff;*/
    border-top: 5px solid #f4f4f4;
  }
  .navlist ul {
    font-size: 0px;
  }
  .navlist ul li {
    display: inline-block;
    text-align: center;
    font-size: 40/@rem;
    border-bottom: 1px solid rgba(208, 234, 234, 0.3);
  }
  .navlist ul li:nth-child(2) {
    border-left: 1px solid rgba(208, 234, 234, 0.3);
  }
  .navlist ul li:nth-child(3) {
    border-left: 1px solid rgba(208, 234, 234, 0.3);
  }
  .navlist ul li:nth-child(4) {
    border-left: 1px solid rgba(208, 234, 234, 0.3);
  }
  .navlist ul li:nth-child(5) {
    border-left: 1px solid rgba(208, 234, 234, 0.3);
    border-right: 1px solid rgba(208, 234, 234, 0.3);
  }
  .navlist ul li:nth-child(8) {
    border-right: 1px solid rgba(208, 234, 234, 0.3);
  }
  .navlist ul li:nth-child(7) {
    border-right: 1px solid rgba(208, 234, 234, 0.3);
    border-left: 1px solid rgba(208, 234, 234, 0.3);
  }
  .navlist ul li:nth-child(9) {
    border-right: 1px solid rgba(208, 234, 234, 0.3);
  }
  .navlist .rowLation p {
    text-align: center;
    transform: translateY(85/@rem);
    font-size: 42/@rem;
    align-content: center;
    font-weight: bold;
    color: #000000;
    margin: 0;
  }
  .navlist .col-20 {
    height: 290/@rem;
    padding: 0;
  }
  .erps_img {
    display: inline-block;
    background: url(../images/OMA/erpIcon_img.png) no-repeat center;
    background-size: 100%;
    width: 80/@rem;
    height: 80/@rem;
    transform: translateY(50/@rem);
  }
  .rwgl_img {
    display: inline-block;
    background: url(../images/OMA/rwgl_img_icon.png) no-repeat center;
    background-size: 100%;
    width: 80/@rem;
    height: 80/@rem;
    transform: translateY(50/@rem);
  }
  .jydl_img {
    display: inline-block;
    background: url(../images/OMA/jydl_img_icon.png) no-repeat center;
    background-size: 100%;
    width: 80/@rem;
    height: 80/@rem;
    transform: translateY(50/@rem);
  }
  .schjy_img {
    display: inline-block;
    background: url(../images/OMA/schjy_img_icon.png) no-repeat center;
    background-size: 100%;
    width: 80/@rem;
    height: 80/@rem;
    transform: translateY(50/@rem);
  }
  .jsdl_img {
    display: inline-block;
    background: url(../images/OMA/jsdl_img_icon.png) no-repeat center;
    background-size: 100%;
    width: 80/@rem;
    height: 80/@rem;
    transform: translateY(50/@rem);
  }
}